

















































































.container {
  width: 100vw;
  min-height: 100vh;
  background: #f0f0f0;
  overflow: auto;
}

.ticket-content {
  margin: 24px;
  background: #fff;
  border-radius: 8px;
  .divider-box {
    position: relative;
    border-bottom: 1px dashed #e6e6e6;
    margin: 40px 0;

    .status {
      position: absolute;
      right: 26px;
      top: -56px;
      width: 112px;
      height: 112px;
    }
    .divider {
      position: absolute;
      top: -16px;
      height: 32px;
      line-height: 32px;
      text-align: center;
      left: 0;
      right: 0;
      font-size: 12px;

      &::before {
        position: absolute;
        top: 50%;
        left: -20px;
        content: "";
        display: block;
        width: 40px;
        height: 40px;
        background: #f0f0f0;
        border-radius: 20px;
        margin-top: -20px;
      }
      &::after {
        position: absolute;
        top: 50%;
        right: -20px;
        content: "";
        display: block;
        width: 40px;
        height: 40px;
        background: #f0f0f0;
        border-radius: 20px;
        margin-top: -20px;
      }
    }
  }
  .code-container {
    .code-box {
      width: 18em;
      margin: 0 auto;
      padding: 10px 20px;
      color: #b2b2b2;
      font-size: 34px;
    }
    .rq {
      opacity: 0.4;
      text-align: center;
      img {
        width: 500px;
        height: 500px;
        margin: 0 auto;
      }
    }
  }
}
.box {
  padding: 30px;
  background: #fff;
  font-size: 30px;
  color: #333;
  line-height: 1.5;
  margin-bottom: 20px;
}
.cinema {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 76px;
  line-height: 76px;
  color: #666;
  padding: 0 30px;
  font-size: 28px;
  border-bottom: 1px solid #f0f0f0;
}

.triangle {
  width: 16px;
  height: 16px;
  border-left: 1px solid #999;
  border-top: 1px solid #999;
  margin-left: 5px;
  transform: rotate(135deg) scale(0.8);
}

.movie-info {
  padding: 0 30px;
  .movie-name {
    font-size: 36px;
    color: #333;
    margin: 20px 0;
  }
  .showTime {
    font-size: 30px;
    color: #f03d37;
  }
  .position {
    font-size: 30px;
    color: #666;
    margin: 10px 0 20px;
  }
}

.phone-box {
  display: flex;
  justify-content: space-between;
  font-size: 28px;
  color: #999;
}

.phone {
  color: #f03d37;
}

.map-box {
  margin-bottom: 20px;
}
